import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View,Panel } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';


class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: {},
			count:0,
			api:null,
		};
	}

	componentDidMount() {
			var sh = 0;
			var arr_2 = [];
			var pi;
			var pi_1;
			var  test7=0;
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					connect.send("VKWebAppCallAPIMethod", {"method": "friends.get", "request_id": "1test", "params": {"user_id": e.detail.data.id, "v":"5.103", "access_token":"0c8b8a7c0c8b8a7c0c8b8a7c6a0ce4a5fa00c8b0c8b8a7c52af213ff35b212b9cc1b948"}});

					break;

				case 'VKWebAppCallAPIMethodResult':
				  switch (e.detail.data.request_id) {
				  	case '1test':
						this.setState({count:e.detail.data.response.count});
						  for (var i = 0; i < e.detail.data.response.count; i++) {
								setTimeout(()=>{
									connect.send("VKWebAppCallAPIMethod", {"method": "friends.get", "request_id": "2test", "params": {"user_id": e.detail.data.response.items[i], "v":"5.103", "access_token":"0c8b8a7c0c8b8a7c0c8b8a7c6a0ce4a5fa00c8b0c8b8a7c52af213ff35b212b9cc1b948"}});
								});

						  }
					   break;



				  	default:

              console.log(e.detail.data);
				  }

					break;
				default:
					console.log(e.detail);


			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	render() {
		return (
			<View activePanel={this.state.activePanel}>
			<Panel id="home">
			  {this.state.count}
			</Panel>
			</View>
		);
	}
}

export default App;
